/*=== 5.6 Hero Background ===*/
.hero-wrap {
  position: relative;
  overflow: hidden;
}
.hero-wrap .hero-mask, .hero-wrap .hero-bg, .hero-wrap .hero-bg-slideshow {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}
.hero-wrap .hero-mask {
  z-index: 1;
}
.hero-wrap .hero-content {
  position: relative;
  z-index: 2;
}
.hero-wrap .hero-particles {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 3;
}
.hero-wrap .hero-bg-slideshow {
  z-index: 0;
}
.hero-wrap .hero-bg {
  z-index: 0;
  background-attachment: fixed;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  transition: background-image 300ms ease-in 200ms;
}
.hero-wrap .hero-bg.hero-bg-scroll {
  background-attachment: scroll;
}
.hero-wrap .hero-bg-slideshow .hero-bg {
  background-attachment: inherit;
}
.hero-wrap .hero-bg-slideshow.owl-carousel .owl-stage-outer, .hero-wrap .hero-bg-slideshow.owl-carousel .owl-stage, .hero-wrap .hero-bg-slideshow.owl-carousel .owl-item {
  height: 100%;
}

/*=== 5.7 Slick Slider ===*/
.slick-slide {
  padding: 0 12px;
}

.slick-dots {
  position: initial;
  margin-top: 10px;
}
.slick-dots li {
  margin: 0;
  width: auto;
  height: auto;
}
.slick-dots li button {
  position: relative;
  width: 20px;
  height: 20px;
  margin: 5px 3px;
  border: 1px solid transparent;
  display: block;
  -webkit-backface-visibility: visible;
  backface-visibility: visible;
  transition: all 0.2s ease;
  border-radius: 30px;
}
.slick-dots li button:before {
  transition: all 0.5s ease;
  width: 10px;
  height: 10px;
  content: "";
  left: 50%;
  top: 50%;
  position: absolute;
  border-radius: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  margin-left: -5px;
  margin-top: -5px;
  opacity: 1;
}
.slick-dots li.slick-active button, .slick-dots li li:hover button {
  border-color: #f5bfda;
  transition: all 0.5s ease;
}
.slick-dots li.slick-active button:before, .slick-dots li li:hover button:before {
  background-color: #f5bfda;
  transition: all 0.5s ease;
}

/*=== 5.8 Brands Grid ===*/
.brands-grid {
  overflow: hidden;
}
.brands-grid > .row > * {
  padding-top: 20px;
  padding-bottom: 20px;
  position: relative;
}
.brands-grid.separator-border > .row > *:after, .brands-grid.separator-border > .row > *:before {
  content: "";
  position: absolute;
}
.brands-grid.separator-border > .row > *:after {
  width: 100%;
  height: 0;
  top: auto;
  left: 0;
  bottom: -1px;
  border-bottom: 1px dotted #e0dede;
}
.brands-grid.separator-border > .row > *:before {
  height: 100%;
  top: 0;
  left: -1px;
  border-left: 1px dotted #e0dede;
}
.brands-grid.separator-border.separator-border-light > .row > *:after {
  border-bottom: 1px dotted rgba(250, 250, 250, 0.15);
}
.brands-grid.separator-border.separator-border-light > .row > *:before {
  border-left: 1px dotted rgba(250, 250, 250, 0.15);
}

